import React, { Component } from 'react'
import { Form, Icon, Input, Button, Checkbox } from 'antd'
import { connect } from 'react-redux'
import { loginUser } from '../redux/reducers/userReducer'

const FormItem = Form.Item

class LoginForm extends Component {
  state = {
    email: '',
    password: '',
    remember: false
  }

  handleUserInput = (state, input) => {
    this.setState({ [state]: input })
  }

  handleCheckbox = () => {
    this.setState(prevProps => ({ remember: !prevProps.remember }))
  }

  handleSubmit = () => {
    const { dispatch } = this.props;
    const { email, password } = this.state;
    dispatch(loginUser(email, password))
  }

  render () {
    const { email, password, remember } = this.state
    console.log(this.props);
    return (
      <Form>
        <FormItem>
          <Input
            type="email"
            onChange={e => this.handleUserInput('email', e.target.value)}
            placeholder='Email'
            value={email}
          />
        </FormItem>

        <FormItem>
          <Input
            type="password"
            onChange={e => this.handleUserInput('password', e.target.value)}
            placeholder='Password'
            value={password}
          />
        </FormItem>

        <FormItem>
          <Checkbox onClick={this.handleCheckbox} checked={remember}>
            Remember me?
          </Checkbox>
        </FormItem>

        <FormItem>
          <Button type='primary' onClick={this.handleSubmit}>Log In</Button>
        </FormItem>
      </Form>
    )
  }
}

const mapStateToProps = state => state;

export default connect(mapStateToProps)(LoginForm)
